"use client";

import { UploadSection } from "@/components/upload-section";
import { VideoConfig } from "@/components/video-config";
import { VideoPreview } from "@/components/video-preview";
import { ProgressBar } from "@/components/progress-bar";
import { Professional3DPreview } from "@/components/professional-3d-preview";
import { AIVideoGenerator } from "@/components/ai-video-generator";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { useState } from "react";

export default function Home() {
  const [images, setImages] = useState<string[]>([]);
  const [config, setConfig] = useState({
    rotationSpeed: 3,
    quality: "1080p",
    backgroundColor: "#000000",
    format: "mp4",
    backgroundType: 'color' as 'color' | 'image' | 'ai-generated',
    backgroundImage: undefined as string | undefined,
    aiPrompt: undefined as string | undefined,
    useAIEnhancement: false,
  });
  const [isGenerating, setIsGenerating] = useState(false);
  const [progress, setProgress] = useState(0);
  const [generatedVideo, setGeneratedVideo] = useState<string | null>(null);

  const handleAIVideoGenerated = (videoUrl: string) => {
    setGeneratedVideo(videoUrl);
  };

  return (
    <main className="min-h-screen bg-black">
      <div className="container mx-auto px-4 py-8">
        {/* Header */}
        <header className="text-center mb-12">
          <h1 className="text-5xl font-bold mb-4 bg-gradient-to-r from-white to-gray-400 bg-clip-text text-transparent">
            Vedavid
          </h1>
          <p className="text-xl text-gray-400 max-w-2xl mx-auto">
            Create stunning rotation videos from your images with AI-powered smooth transitions
          </p>
        </header>

        {/* Main Content */}
        <div className="max-w-6xl mx-auto">
          <Tabs defaultValue="upload" className="w-full">
            <TabsList className="grid w-full grid-cols-2 bg-gray-800 mb-8">
              <TabsTrigger value="upload" className="text-white data-[state=active]:bg-gray-700">
                Upload Images
              </TabsTrigger>
              <TabsTrigger value="ai-generate" className="text-white data-[state=active]:bg-gray-700">
                AI Generate
              </TabsTrigger>
            </TabsList>

            <TabsContent value="upload" className="space-y-8">
              <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                {/* Left Column - Upload & Config */}
                <div className="space-y-6">
                  <UploadSection 
                    images={images} 
                    setImages={setImages} 
                  />
                  
                  {images.length >= 2 && (
                    <VideoConfig 
                      config={config} 
                      setConfig={setConfig} 
                    />
                  )}
                </div>

                {/* Right Column - Preview & Progress */}
                <div className="space-y-6">
                  {isGenerating && (
                    <ProgressBar progress={progress} />
                  )}
                  
                  {images.length >= 2 && (
                    <Professional3DPreview 
                      images={images}
                      config={config}
                    />
                  )}
                  
                  <VideoPreview 
                    images={images}
                    config={config}
                    isGenerating={isGenerating}
                    setIsGenerating={setIsGenerating}
                    setProgress={setProgress}
                    generatedVideo={generatedVideo}
                    setGeneratedVideo={setGeneratedVideo}
                  />
                </div>
              </div>
            </TabsContent>

            <TabsContent value="ai-generate" className="space-y-8">
              <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                {/* Left Column - AI Generator */}
                <div className="space-y-6">
                  <AIVideoGenerator 
                    onVideoGenerated={handleAIVideoGenerated}
                  />
                </div>

                {/* Right Column - AI Generated Preview */}
                <div className="space-y-6">
                  <VideoPreview 
                    images={[]}
                    config={config}
                    isGenerating={isGenerating}
                    setIsGenerating={setIsGenerating}
                    setProgress={setProgress}
                    generatedVideo={generatedVideo}
                    setGeneratedVideo={setGeneratedVideo}
                  />
                </div>
              </div>
            </TabsContent>
          </Tabs>
        </div>
      </div>
    </main>
  );
}
