"use client";

import { useCallback, useState } from "react";
import { useDropzone } from "react-dropzone";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { ImageIcon, Upload, X } from "lucide-react";

interface UploadSectionProps {
  images: string[];
  setImages: (images: string[]) => void;
}

export function UploadSection({ images, setImages }: UploadSectionProps) {
  const [isDragging, setIsDragging] = useState(false);

  const onDrop = useCallback((acceptedFiles: File[]) => {
    const newImages = acceptedFiles.map((file) => URL.createObjectURL(file));
    setImages([...images, ...newImages].slice(0, 10)); // Allow up to 10 images
  }, [images, setImages]);

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: {
      'image/*': ['.jpeg', '.jpg', '.png', '.webp']
    },
    maxFiles: 10 - images.length,
    maxSize: 10 * 1024 * 1024, // 10MB
  });

  const removeImage = (index: number) => {
    setImages(images.filter((_, i) => i !== index));
  };

  return (
    <Card className="bg-gray-900 border-gray-800">
      <CardHeader>
        <CardTitle className="text-white">Upload Images</CardTitle>
        <CardDescription className="text-gray-400">
          Upload 2-10 images to create professional 360° rotation video
        </CardDescription>
      </CardHeader>
      <CardContent>
        <div
          {...getRootProps()}
          className={`
            border-2 border-dashed rounded-lg p-8 text-center cursor-pointer transition-colors
            ${isDragActive ? 'border-blue-500 bg-blue-500/10' : 'border-gray-700 hover:border-gray-600'}
            ${images.length >= 10 ? 'opacity-50 cursor-not-allowed' : ''}
          `}
        >
          <input {...getInputProps()} />
          <Upload className="mx-auto h-12 w-12 text-gray-400 mb-4" />
          <p className="text-gray-300">
            {isDragActive ? 'Drop images here...' : 'Drag & drop images or click to select'}
          </p>
          <p className="text-sm text-gray-500 mt-2">
            Supports: JPEG, PNG, WebP (max 10MB each) - {images.length}/10 uploaded
          </p>
          <p className="text-xs text-gray-600 mt-1">
            Minimum 2 images required for video generation
          </p>
        </div>

        {images.length > 0 && (
          <div className="mt-6 space-y-4">
            <h3 className="text-sm font-medium text-white">Uploaded Images ({images.length}/10)</h3>
            <div className="grid grid-cols-2 md:grid-cols-5 gap-2">
              {images.map((image, index) => (
                <div key={index} className="relative group">
                  <img
                    src={image}
                    alt={`View ${index + 1}`}
                    className="w-full h-24 object-cover rounded-lg"
                  />
                  <Button
                    variant="ghost"
                    size="icon"
                    className="absolute top-1 right-1 bg-black/50 hover:bg-black/70 h-6 w-6"
                    onClick={() => removeImage(index)}
                  >
                    <X className="h-3 w-3" />
                  </Button>
                  <div className="absolute bottom-1 left-1 bg-black/50 px-1 py-0.5 rounded text-xs">
                    {index + 1}
                  </div>
                </div>
              ))}
            </div>
            {images.length < 2 && (
              <p className="text-yellow-400 text-sm">
                Upload at least 2 images to generate rotation video
              </p>
            )}
          </div>
        )}
      </CardContent>
    </Card>
  );
}
