"use client";

import { useState } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Wand2, Download, Loader2 } from "lucide-react";

interface AIVideoGeneratorProps {
  onVideoGenerated: (videoUrl: string) => void;
}

export function AIVideoGenerator({ onVideoGenerated }: AIVideoGeneratorProps) {
  const [prompt, setPrompt] = useState("");
  const [style, setStyle] = useState("realistic");
  const [duration, setDuration] = useState("3");
  const [isGenerating, setIsGenerating] = useState(false);
  const [progress, setProgress] = useState(0);
  const [error, setError] = useState<string | null>(null);

  const generateAIVideo = async () => {
    if (!prompt.trim()) {
      setError("Please enter a prompt to generate video");
      return;
    }

    setIsGenerating(true);
    setProgress(0);
    setError(null);

    try {
      // Simulate AI video generation process
      for (let i = 0; i <= 100; i += 5) {
        setProgress(i);
        await new Promise(resolve => setTimeout(resolve, 200));
      }

      // Create a simulated video URL (in production, this would call actual AI API)
      const simulatedVideoUrl = `data:video/mp4;base64,${btoa(`ai_generated_video_${Date.now()}`)}`;
      onVideoGenerated(simulatedVideoUrl);
      
    } catch (error) {
      console.error("Error generating AI video:", error);
      setError("Failed to generate AI video. Please try again.");
    } finally {
      setIsGenerating(false);
    }
  };

  const predefinedPrompts = [
    "A luxury handbag rotating on a marble pedestal with soft studio lighting",
    "A modern smartphone spinning in a futuristic tech environment",
    "A vintage watch rotating on a wooden surface with warm lighting",
    "A sneaker rotating in a colorful urban street setting",
    "A perfume bottle spinning in an elegant minimalist studio"
  ];

  return (
    <Card className="bg-gray-900 border-gray-800">
      <CardHeader>
        <CardTitle className="text-white flex items-center gap-2">
          <Wand2 className="h-5 w-5" />
          AI Video Generator
        </CardTitle>
        <CardDescription className="text-gray-400">
          Generate professional rotation videos using AI prompts
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-6">
        {/* Prompt Input */}
        <div>
          <Label className="text-white mb-2 block">Video Description</Label>
          <Textarea
            placeholder="Describe the product and scene you want to create (e.g., 'A luxury watch rotating on a marble surface with dramatic lighting')"
            value={prompt}
            onChange={(e) => setPrompt(e.target.value)}
            className="bg-gray-800 border-gray-700 text-white"
            rows={4}
          />
        </div>

        {/* Predefined Prompts */}
        <div>
          <Label className="text-white mb-2 block">Quick Prompts</Label>
          <div className="grid grid-cols-1 gap-2">
            {predefinedPrompts.map((predefinedPrompt, index) => (
              <Button
                key={index}
                variant="outline"
                size="sm"
                onClick={() => setPrompt(predefinedPrompt)}
                className="text-left justify-start h-auto p-2 text-xs bg-gray-800 border-gray-700 text-gray-300 hover:bg-gray-700"
              >
                {predefinedPrompt}
              </Button>
            ))}
          </div>
        </div>

        {/* Style Selection */}
        <div>
          <Label className="text-white mb-2 block">Video Style</Label>
          <Select value={style} onValueChange={setStyle}>
            <SelectTrigger className="bg-gray-800 border-gray-700 text-white">
              <SelectValue />
            </SelectTrigger>
            <SelectContent className="bg-gray-800 border-gray-700">
              <SelectItem value="realistic">Realistic</SelectItem>
              <SelectItem value="cinematic">Cinematic</SelectItem>
              <SelectItem value="minimalist">Minimalist</SelectItem>
              <SelectItem value="artistic">Artistic</SelectItem>
              <SelectItem value="futuristic">Futuristic</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {/* Duration */}
        <div>
          <Label className="text-white mb-2 block">Duration</Label>
          <Select value={duration} onValueChange={setDuration}>
            <SelectTrigger className="bg-gray-800 border-gray-700 text-white">
              <SelectValue />
            </SelectTrigger>
            <SelectContent className="bg-gray-800 border-gray-700">
              <SelectItem value="2">2 seconds</SelectItem>
              <SelectItem value="3">3 seconds</SelectItem>
              <SelectItem value="5">5 seconds</SelectItem>
              <SelectItem value="10">10 seconds</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {/* Progress */}
        {isGenerating && (
          <div className="space-y-2">
            <div className="flex justify-between text-sm">
              <span className="text-white">Generating AI video...</span>
              <span className="text-gray-400">{progress}%</span>
            </div>
            <div className="w-full bg-gray-700 rounded-full h-2">
              <div 
                className="bg-blue-600 h-2 rounded-full transition-all duration-300"
                style={{ width: `${progress}%` }}
              />
            </div>
          </div>
        )}

        {/* Error Message */}
        {error && (
          <div className="text-red-400 text-sm">
            {error}
          </div>
        )}

        {/* Generate Button */}
        <Button
          onClick={generateAIVideo}
          disabled={isGenerating || !prompt.trim()}
          className="w-full"
          size="lg"
        >
          {isGenerating ? (
            <>
              <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              Generating AI Video...
            </>
          ) : (
            <>
              <Wand2 className="mr-2 h-4 w-4" />
              Generate AI Video
            </>
          )}
        </Button>

        {/* Info */}
        <div className="text-xs text-gray-500 text-center">
          AI video generation creates professional product rotation videos from text descriptions
        </div>
      </CardContent>
    </Card>
  );
}
