"use client";

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Slider } from "@/components/ui/slider";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Label } from "@/components/ui/label";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Switch } from "@/components/ui/switch";
import { Upload } from "lucide-react";

interface VideoConfigProps {
  config: {
    rotationSpeed: number;
    quality: string;
    backgroundColor: string;
    format: string;
    backgroundType: 'color' | 'image' | 'ai-generated';
    backgroundImage?: string;
    aiPrompt?: string;
    useAIEnhancement: boolean;
  };
  setConfig: (config: any) => void;
}

export function VideoConfig({ config, setConfig }: VideoConfigProps) {
  const handleBackgroundImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      const url = URL.createObjectURL(file);
      setConfig({ ...config, backgroundImage: url });
    }
  };

  return (
    <Card className="bg-gray-900 border-gray-800">
      <CardHeader>
        <CardTitle className="text-white">Video Settings</CardTitle>
        <CardDescription className="text-gray-400">
          Configure your professional 360° rotation video
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-6">
        {/* Rotation Speed */}
        <div>
          <Label className="text-white mb-2 block">Rotation Speed</Label>
          <Slider
            value={[config.rotationSpeed]}
            onValueChange={([value]) => setConfig({ ...config, rotationSpeed: value })}
            min={1}
            max={10}
            step={0.5}
            className="w-full"
          />
          <div className="text-sm text-gray-400 mt-1">
            {config.rotationSpeed} seconds per rotation
          </div>
        </div>

        {/* Quality */}
        <div>
          <Label className="text-white mb-2 block">Video Quality</Label>
          <Select
            value={config.quality}
            onValueChange={(value) => setConfig({ ...config, quality: value })}
          >
            <SelectTrigger className="bg-gray-800 border-gray-700 text-white">
              <SelectValue />
            </SelectTrigger>
            <SelectContent className="bg-gray-800 border-gray-700">
              <SelectItem value="720p">720p (HD)</SelectItem>
              <SelectItem value="1080p">1080p (Full HD)</SelectItem>
              <SelectItem value="4k">4K (Ultra HD)</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {/* Background Type */}
        <div>
          <Label className="text-white mb-2 block">Background Type</Label>
          <Select
            value={config.backgroundType}
            onValueChange={(value) => setConfig({ ...config, backgroundType: value })}
          >
            <SelectTrigger className="bg-gray-800 border-gray-700 text-white">
              <SelectValue />
            </SelectTrigger>
            <SelectContent className="bg-gray-800 border-gray-700">
              <SelectItem value="color">Solid Color</SelectItem>
              <SelectItem value="image">Custom Image</SelectItem>
              <SelectItem value="ai-generated">AI Generated</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {/* Background Options */}
        {config.backgroundType === 'color' && (
          <div>
            <Label className="text-white mb-2 block">Background Color</Label>
            <div className="flex items-center space-x-2">
              <input
                type="color"
                value={config.backgroundColor}
                onChange={(e) => setConfig({ ...config, backgroundColor: e.target.value })}
                className="w-12 h-12 rounded cursor-pointer"
              />
              <span className="text-sm text-gray-400">{config.backgroundColor}</span>
            </div>
          </div>
        )}

        {config.backgroundType === 'image' && (
          <div>
            <Label className="text-white mb-2 block">Background Image</Label>
            <Input
              type="file"
              accept="image/*"
              onChange={handleBackgroundImageUpload}
              className="bg-gray-800 border-gray-700 text-white"
            />
            {config.backgroundImage && (
              <img 
                src={config.backgroundImage} 
                alt="Background" 
                className="mt-2 w-full h-20 object-cover rounded"
              />
            )}
          </div>
        )}

        {config.backgroundType === 'ai-generated' && (
          <div>
            <Label className="text-white mb-2 block">AI Background Prompt</Label>
            <Textarea
              placeholder="Describe the background you want (e.g., 'clean white studio with soft lighting')"
              value={config.aiPrompt || ''}
              onChange={(e) => setConfig({ ...config, aiPrompt: e.target.value })}
              className="bg-gray-800 border-gray-700 text-white"
              rows={3}
            />
          </div>
        )}

        {/* AI Enhancement */}
        <div className="flex items-center justify-between">
          <Label className="text-white">AI Enhancement</Label>
          <Switch
            checked={config.useAIEnhancement}
            onCheckedChange={(checked) => setConfig({ ...config, useAIEnhancement: checked })}
          />
        </div>

        {/* Format */}
        <div>
          <Label className="text-white mb-2 block">Output Format</Label>
          <Select
            value={config.format}
            onValueChange={(value) => setConfig({ ...config, format: value })}
          >
            <SelectTrigger className="bg-gray-800 border-gray-700 text-white">
              <SelectValue />
            </SelectTrigger>
            <SelectContent className="bg-gray-800 border-gray-700">
              <SelectItem value="mp4">MP4</SelectItem>
              <SelectItem value="webm">WebM</SelectItem>
              <SelectItem value="gif">GIF</SelectItem>
            </SelectContent>
          </Select>
        </div>
      </CardContent>
    </Card>
  );
}
