"use client";

import * as THREE from 'three';

export class Professional3DRotator {
  private scene: THREE.Scene;
  private camera: THREE.PerspectiveCamera;
  private renderer: THREE.WebGLRenderer;
  private product: THREE.Group | null = null;
  private textures: THREE.Texture[] = [];
  private config: any;
  private canvas: HTMLCanvasElement;
  private ambientLight: THREE.AmbientLight | null = null;
  private directionalLight: THREE.DirectionalLight | null = null;
  private spotLight: THREE.SpotLight | null = null;

  constructor(canvas: HTMLCanvasElement, config: any) {
    this.canvas = canvas;
    this.config = config;
    
    // Create scene with professional lighting
    this.scene = new THREE.Scene();
    this.scene.background = new THREE.Color(config.backgroundColor);

    // Professional camera setup (like product photography)
    const aspect = 16 / 9;
    this.camera = new THREE.PerspectiveCamera(
      35, // Professional product photography angle
      aspect,
      0.1,
      1000
    );
    this.camera.position.set(0, 1.2, 4); // Professional product distance

    // Professional renderer setup
    this.renderer = new THREE.WebGLRenderer({ 
      canvas: this.canvas, 
      antialias: true,
      alpha: true,
      powerPreference: "high-performance"
    });
    
    // Set canvas size based on quality
    const qualityMap = {
      '720p': { width: 1280, height: 720 },
      '1080p': { width: 1920, height: 1080 },
      '4k': { width: 1920, height: 1080 }
    };
    
    const { width, height } = qualityMap[this.config.quality as keyof typeof qualityMap];
    this.renderer.setSize(width, height);
    this.renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
    this.renderer.shadowMap.enabled = true;
    this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
    this.renderer.outputColorSpace = THREE.SRGBColorSpace;

    // Professional lighting setup (like product photography)
    this.setupProfessionalLighting();
  }

  private setupProfessionalLighting() {
    // Ambient light for overall illumination
    this.ambientLight = new THREE.AmbientLight(0xffffff, 0.4);
    this.scene.add(this.ambientLight);

    // Main directional light (key light)
    this.directionalLight = new THREE.DirectionalLight(0xffffff, 0.8);
    this.directionalLight.position.set(5, 10, 7.5);
    this.directionalLight.castShadow = true;
    this.directionalLight.shadow.mapSize.width = 2048;
    this.directionalLight.shadow.mapSize.height = 2048;
    this.directionalLight.shadow.camera.near = 0.5;
    this.directionalLight.shadow.camera.far = 50;
    this.scene.add(this.directionalLight);

    // Spot light for highlights
    this.spotLight = new THREE.SpotLight(0xffffff, 0.6);
    this.spotLight.position.set(-5, 10, 5);
    this.spotLight.angle = Math.PI / 6;
    this.spotLight.penumbra = 0.2;
    this.spotLight.castShadow = true;
    this.scene.add(this.spotLight);
  }

  async loadImages(imageUrls: string[]): Promise<void> {
    this.textures = [];
    const loader = new THREE.TextureLoader();
    
    for (const url of imageUrls) {
      const texture = await new Promise<THREE.Texture>((resolve, reject) => {
        loader.load(
          url,
          (texture) => {
            texture.colorSpace = THREE.SRGBColorSpace;
            texture.wrapS = THREE.RepeatWrapping;
            texture.wrapT = THREE.ClampToEdgeWrapping;
            resolve(texture);
          },
          undefined,
          reject
        );
      });
      this.textures.push(texture);
    }

    this.createProfessionalProduct();
  }

  private createProfessionalProduct() {
    // Remove existing product
    if (this.product) {
      this.scene.remove(this.product);
    }

    this.product = new THREE.Group();

    // Create professional product display
    if (this.textures.length >= 3) {
      // Create cylindrical product for 360-degree rotation
      const geometry = new THREE.CylinderGeometry(1.2, 1.2, 2.2, 128, 1, false);
      
      // Create panoramic texture from images
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d')!;
      canvas.width = 4096;
      canvas.height = 2048;

      // Create seamless 360-degree texture
      const imageWidth = canvas.width / 4;
      const imageHeight = canvas.height;

      // Draw images in sequence for smooth 360-degree view
      ctx.fillStyle = '#f0f0f0';
      ctx.fillRect(0, 0, canvas.width, canvas.height);

      // Create smooth transitions between images
      if (this.textures[0]) {
        ctx.drawImage(this.textures[0].image, 0, 0, imageWidth, imageHeight);
      }
      if (this.textures[2]) {
        ctx.drawImage(this.textures[2].image, imageWidth, 0, imageWidth, imageHeight);
      }
      if (this.textures[1]) {
        ctx.drawImage(this.textures[1].image, imageWidth * 2, 0, imageWidth, imageHeight);
      }
      if (this.textures[2]) {
        ctx.drawImage(this.textures[2].image, imageWidth * 3, 0, imageWidth, imageHeight);
      }

      const panoramicTexture = new THREE.CanvasTexture(canvas);
      panoramicTexture.wrapS = THREE.RepeatWrapping;
      panoramicTexture.wrapT = THREE.ClampToEdgeWrapping;

      // Professional material
      const material = new THREE.MeshStandardMaterial({ 
        map: panoramicTexture,
        roughness: 0.2,
        metalness: 0.1,
        envMapIntensity: 0.5
      });

      const mesh = new THREE.Mesh(geometry, material);
      mesh.castShadow = true;
      mesh.receiveShadow = true;
      this.product.add(mesh);

      // Add subtle platform
      const platformGeometry = new THREE.CylinderGeometry(2, 2, 0.1, 64);
      const platformMaterial = new THREE.MeshStandardMaterial({ 
        color: 0xcccccc,
        roughness: 0.8,
        metalness: 0.2
      });
      const platform = new THREE.Mesh(platformGeometry, platformMaterial);
      platform.position.y = -1.2;
      platform.receiveShadow = true;
      this.product.add(platform);

    } else {
      // Fallback for fewer images
      const geometry = new THREE.BoxGeometry(1.5, 2, 1.5);
      const texture = this.textures[0] || new THREE.Texture();
      const material = new THREE.MeshStandardMaterial({ 
        map: texture,
        roughness: 0.3,
        metalness: 0.1
      });
      
      const mesh = new THREE.Mesh(geometry, material);
      mesh.castShadow = true;
      mesh.receiveShadow = true;
      this.product.add(mesh);
    }

    this.scene.add(this.product);
  }

  async generateProfessionalVideo(
    onProgress?: (progress: number) => void
  ): Promise<string> {
    const fps = 60; // Professional 60fps
    const duration = this.config.rotationSpeed;
    const totalFrames = fps * duration;

    // Professional video settings
    const qualityMap = {
      '720p': { width: 1280, height: 720 },
      '1080p': { width: 1920, height: 1080 },
      '4k': { width: 1920, height: 1080 }
    };
    
    const { width, height } = qualityMap[this.config.quality as keyof typeof qualityMap];
    this.renderer.setSize(width, height);

    // Professional video encoding
    const mimeTypes = [
      'video/webm;codecs=vp9',
      'video/webm;codecs=vp8',
      'video/webm',
      'video/mp4'
    ];
    
    let selectedMimeType = '';
    for (const mimeType of mimeTypes) {
      if (MediaRecorder.isTypeSupported(mimeType)) {
        selectedMimeType = mimeType;
        break;
      }
    }

    if (!selectedMimeType) {
      throw new Error('No supported video format found');
    }

    const stream = this.canvas.captureStream(fps);
    const mediaRecorder = new MediaRecorder(stream, {
      mimeType: selectedMimeType,
      videoBitsPerSecond: 5000000 // High quality
    });

    const chunks: Blob[] = [];
    mediaRecorder.ondataavailable = (event) => {
      if (event.data.size > 0) {
        chunks.push(event.data);
      }
    };

    return new Promise((resolve, reject) => {
      mediaRecorder.onstop = () => {
        const blob = new Blob(chunks, { type: selectedMimeType });
        const url = URL.createObjectURL(blob);
        resolve(url);
      };

      mediaRecorder.onerror = (error) => {
        reject(new Error(`MediaRecorder error: ${error}`));
      };

      mediaRecorder.start();

      let frame = 0;
      const animate = () => {
        if (frame >= totalFrames || !this.product) {
          mediaRecorder.stop();
          return;
        }

        // Smooth 360-degree rotation around Y axis (like product photography)
        const rotationAngle = (frame / totalFrames) * Math.PI * 2;
        this.product.rotation.y = rotationAngle;
        
        // Keep X and Z rotation minimal for clean product rotation
        this.product.rotation.x = 0;
        this.product.rotation.z = 0;

        // Render with professional lighting
        this.renderer.render(this.scene, this.camera);
        
        if (onProgress) {
          onProgress((frame / totalFrames) * 100);
        }

        frame++;
        setTimeout(animate, 1000 / fps);
      };

      animate();
    });
  }

  // Real-time preview method
  renderFrame(angle: number) {
    if (!this.product) return;

    this.product.rotation.y = angle;
    this.renderer.render(this.scene, this.camera);
  }

  // Cleanup
  dispose() {
    this.textures.forEach(texture => texture.dispose());
    if (this.product) {
      this.product.traverse((child) => {
        if (child instanceof THREE.Mesh) {
          child.geometry.dispose();
          if (Array.isArray(child.material)) {
            child.material.forEach(material => material.dispose());
          } else {
            child.material.dispose();
          }
        }
      });
    }
    this.renderer.dispose();
  }
}
