"use client";

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Play, Download, Loader2 } from "lucide-react";
import { useState, useRef, useEffect } from "react";
import { Professional3DRotator } from "@/lib/professional-3d-rotator";

interface VideoPreviewProps {
  images: string[];
  config: {
    rotationSpeed: number;
    quality: string;
    backgroundColor: string;
    format: string;
  };
  isGenerating: boolean;
  setIsGenerating: (generating: boolean) => void;
  setProgress: (progress: number) => void;
  generatedVideo: string | null;
  setGeneratedVideo: (video: string | null) => void;
}

export function VideoPreview({
  images,
  config,
  isGenerating,
  setIsGenerating,
  setProgress,
  generatedVideo,
  setGeneratedVideo,
}: VideoPreviewProps) {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [error, setError] = useState<string | null>(null);

  // Generate rotation video
  const generateVideo = async () => {
    if (images.length === 0) {
      setError("Please upload images first");
      return;
    }

    setIsGenerating(true);
    setProgress(0);
    setError(null);

    try {
      // Create hidden canvas for video generation
      const canvas = document.createElement('canvas');
      const generator = new Professional3DRotator(canvas, config);
      
      await generator.loadImages(images);
      
      let videoUrl: string;
      
      if (config.format === 'gif') {
        videoUrl = await generator.generateProfessionalVideo((progress: number) => {
          setProgress(progress);
        });
      } else {
        videoUrl = await generator.generateProfessionalVideo((progress: number) => {
          setProgress(progress);
        });
      }
      
      setGeneratedVideo(videoUrl);
      setProgress(100);
    } catch (error) {
      console.error("Error generating video:", error);
      setError("Failed to generate video. Please try again.");
    } finally {
      setIsGenerating(false);
    }
  };

  const downloadVideo = () => {
    if (!generatedVideo) return;

    const link = document.createElement("a");
    link.href = generatedVideo;
    link.download = `vedavid-rotation-${Date.now()}.${config.format}`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  return (
    <Card className="bg-gray-900 border-gray-800">
      <CardHeader>
        <CardTitle className="text-white">Video Preview</CardTitle>
        <CardDescription className="text-gray-400">
          Preview your rotation video before downloading
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-6">
        {/* Hidden canvas for video generation */}
        <canvas ref={canvasRef} style={{ display: 'none' }} />
        
        {/* Preview Area */}
        <div className="aspect-video bg-gray-800 rounded-lg flex items-center justify-center">
          {generatedVideo ? (
            <video
              src={generatedVideo}
              controls
              className="w-full h-full rounded-lg"
              style={{ backgroundColor: config.backgroundColor }}
            />
          ) : (
            <div className="text-center">
              <Play className="mx-auto h-12 w-12 text-gray-400 mb-4" />
              <p className="text-gray-400">
                {images.length > 0 ? "Ready to generate" : "Upload images to preview"}
              </p>
            </div>
          )}
        </div>

        {/* Error Message */}
        {error && (
          <div className="text-red-400 text-sm text-center">
            {error}
          </div>
        )}

        {/* Controls */}
        <div className="flex space-x-4">
          <Button
            onClick={generateVideo}
            disabled={isGenerating || images.length === 0}
            className="flex-1"
            size="lg"
          >
            {isGenerating ? (
              <>
                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                Generating...
              </>
            ) : (
              <>
                <Play className="mr-2 h-4 w-4" />
                Generate Video
              </>
            )}
          </Button>

          <Button
            onClick={downloadVideo}
            disabled={!generatedVideo || isGenerating}
            variant="outline"
            className="flex-1"
            size="lg"
          >
            <Download className="mr-2 h-4 w-4" />
            Download
          </Button>
        </div>

        {/* Instructions */}
        {images.length === 0 && (
          <div className="text-center text-sm text-gray-400">
            <p>Upload 3 images (front, back, side views) to create your rotation video</p>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
