"use client";

import { useEffect, useRef, useState } from "react";
import * as THREE from 'three';
import { Professional3DRotator } from '@/lib/professional-3d-rotator';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";

interface Professional3DPreviewProps {
  images: string[];
  config: {
    backgroundColor: string;
  };
}

export function Professional3DPreview({ images, config }: Professional3DPreviewProps) {
  const mountRef = useRef<HTMLDivElement>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [rotator, setRotator] = useState<Professional3DRotator | null>(null);

  useEffect(() => {
    if (!mountRef.current || images.length === 0) return;

    setIsLoading(true);

    // Create professional 3D preview
    const canvas = document.createElement('canvas');
    mountRef.current.appendChild(canvas);
    const newRotator = new Professional3DRotator(canvas, {
      ...config,
      quality: '1080p'
    });

    const loadImages = async () => {
      try {
        await newRotator.loadImages(images);
        setRotator(newRotator);
        setIsLoading(false);
        
        // Start smooth rotation animation
        let angle = 0;
        const animate = () => {
          if (newRotator) {
            angle += 0.01;
            newRotator.renderFrame(angle);
            requestAnimationFrame(animate);
          }
        };
        animate();
      } catch (error) {
        console.error('Error loading 3D preview:', error);
        setIsLoading(false);
      }
    };

    loadImages();

    // Cleanup
    return () => {
      if (newRotator) {
        newRotator.dispose();
      }
    };
  }, [images, config.backgroundColor]);

  return (
    <Card className="bg-gray-900 border-gray-800">
      <CardHeader>
        <CardTitle className="text-white">Professional 3D Preview</CardTitle>
        <CardDescription className="text-gray-400">
          Smooth 360° product rotation preview
        </CardDescription>
      </CardHeader>
      <CardContent>
        <div 
          ref={mountRef} 
          className="aspect-video bg-gray-800 rounded-lg"
          style={{ minHeight: '300px' }}
        >
          {isLoading && (
            <div className="flex items-center justify-center h-full">
              <div className="text-gray-400">Loading professional 3D model...</div>
            </div>
          )}
          {images.length === 0 && (
            <div className="flex items-center justify-center h-full">
              <div className="text-gray-400">Upload images to see 3D preview</div>
            </div>
          )}
        </div>
      </CardContent>
    </Card>
  );
}
